@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
body{margin:0;font-family:'Roboto Slab', serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.tile-title{margin:10px 0;border-bottom:0;text-align:center;border-radius:5px;text-transform:uppercase;background-color:#F7AF1F}.tile-timeline{display:flex;flex-direction:column;padding:4px 0}.tile-content{display:flex;flex-direction:row;flex:1 1}.tile-card{padding:25px 0 25px;width:50%;background-color:#F7AF1F}.tile-card p,.tile-card i{margin:10px 0}.tile-card h4{padding:0 25px;margin:10px 0}.tile .card-header{border-bottom:4px solid #fff}.tile .card-project{margin:0 25px 25px;border-bottom:1px solid;padding-bottom:25px}.tile .card-project i{display:block}.tile .card-project:last-child{border-bottom:0}.tile .card-01{display:flex;flex-direction:column;justify-content:flex-start;text-align:right;border-right:4px dotted #fff;border-radius:5px 0 0 5px}.tile .card-01 h3{z-index:1}.tile .card-01 h3::after{content:"";display:inline-block;width:6px;height:6px;border-radius:50%;background-color:#F7AF1F;z-index:1;border:4px solid #fff;margin-bottom:-14px;margin-right:-8px}.tile .card-02{display:flex;flex-direction:column;justify-content:flex-end;text-align:left;border-radius:0 5px 5px 0}.tile .card-02 h3::before{content:"";display:inline-block;width:6px;height:6px;border-radius:50%;background-color:#F7AF1F;z-index:1;border:4px solid #fff;margin-bottom:-14px;margin-left:-8px}.tile .card-02 .empty-container{height:80px}.tile-separator{border-left:4px dotted white}@media (max-width: 768px){.tile-content{flex-direction:column}.tile-card{width:100%}.tile .card-header{text-align:center}.tile .card-project{margin:0 25px}.tile .card-01{text-align:justify;border-right:0;margin-bottom:25px;border-radius:5px}.tile .card-01 h3::after{display:none}.tile .card-02{text-align:justify;border-radius:5px}.tile .card-02 h3::before{display:none}.tile .card-02 .empty-container{display:none}}

.tile-profile{display:flex;flex-direction:column;align-items:center;padding:25px;text-align:center;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(255,0,0,0)), to(#e94e36));background-image:linear-gradient(to bottom, rgba(255,0,0,0), #e94e36)}@media (max-width: 768px){.tile-profile{padding:10px}}.tile-profile .avatar{height:150px;width:150px;border-radius:50%}.tile-profile .site-links{padding:10px 0}.tile-profile h1{text-transform:uppercase;letter-spacing:3px;text-align:justify}.tile-profile a{color:#fff;text-decoration:none}.tile-profile a:hover{text-decoration:underline}.tile-profile p{word-break:break-word}.tile-profile i{font-size:2em;margin:0 10px}

.tile-footer{padding:10px;text-align:center;background-color:#F7AF1F}

.app-container{width:60%;margin:auto}.app-container .tile{color:#fff;margin:25px;border-radius:5px}@media (max-width: 768px){.app-container{width:100%}}

